.footer {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	//background: url('@{bg}footer-bg.jpg') repeat-y top left;
	background-color: rgba(34,65,67,.55);
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		padding: 18px 0;
		@media(max-width: 770px) {
			flex-direction: column;
		}
	}
	&__phone {
		.ff-r-900();
		font-size: 27px;
		color: rgba(255,255,255,.3);
		transition: 0.2s;
		&:hover {
			color: rgba(255,255,255,.5);
		}
	}
}
.copyryght {
	font-size: 18px;
	color: rgba(255,255,255,.55);
}
.social-btn {
	display: flex;
	justify-content: space-between;
	align-items: center;
	@media(max-width: 770px) {
		margin-top: 10px;
	}
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 40px;
		margin: 3px;
		font-size: 0;
		transition: 0.2s;

	}
	&__icon {
		filter: drop-shadow(8px 8px 10px #000000)
	}
	&__link--twit {
		background-color: #33ccff;
		&:hover {
			background-color: darken(#33ccff, 10%);
		}
	}
	&__link--fb {
		background-color: #6699cc;
		&:hover {
			background-color: darken(#6699cc, 10%);
		}
	}
	&__link--vk {
		background-color: #666699;
		&:hover {
			background-color: darken(#666699, 10%);
		}
	}
}
.phone-link {
	text-decoration: none;
	@media(max-width: 770px) {
		margin-top: 10px;
	}
}