.journal-s {
	padding-top: 100px;
	padding-bottom: 100px;
	@media(max-width:1200px) {
		padding-top: 50px;
	}
}
.journal-articles {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	margin-top: 50px;
	box-shadow: 0 0 21px rgba(6, 6, 6, 0.13);
	transition: 0.4s ease;
	&:hover {
		box-shadow: none;
	}
	@media (max-width: 1200px) {
		margin-top: 35px;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr;
	}
	@media (max-width: 770px) {
		display: block;
	}
}
.journal-article {
	&:nth-child(1) {
		grid-column: 1/span 2;
		grid-row: 1/span 1;
		@media (max-width: 1200px) {
			grid-column: 1;
			grid-row: 1/span 1;
		}
	}
	&:nth-child(2) {
		grid-column: 1/span 2;
		grid-row: 2/span 1;
		@media (max-width: 1200px) {
			grid-column: 1;
			grid-row: 2/span 1;
		}
	}
	&:nth-child(3) {
		grid-column: 3/span 1;
		grid-row: 1/span 2;
		@media (max-width: 1200px) {
			grid-column: 1;
			grid-row: 3/span 1;
		}
	}
	&__link {
		position: relative;
		display: grid;
		grid-template-columns: 1fr 1fr;
		color: #000000;
		text-decoration: none;
		text-transform: uppercase;
		transition: 0.4s ease;
		&:hover {
			z-index: 100;
			box-shadow: 0 0 21px rgba(6, 6, 6, 0.23);
		}
		@media (max-width: 770px) {
			display: block;
		}
	}
		&__link--column {
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 1fr;
			@media (max-width: 1200px) {
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 1fr;
			}
		}
	&__img {
		position: relative;
		font-size: 0;
		height: 379px;
		background-color: #616462;
		@media (max-width: 1200px) {
			height: auto;
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	&__content {
		position: relative;
		align-items: center;
		padding: 45px 35px 65px 35px;
		background-color: #ffffff;
		&::after {
			content:'';
			position: absolute;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 10px 10px 10px 10px;
		}
		&--left-tr {
			&::after {
				top: 50px;
				left: -20px;
				border-color: transparent #ffffff transparent transparent;
				@media (max-width: 770px) {
					top: -20px;
					left: 50px;
					border-color: transparent transparent #ffffff transparent ;
				}
			}
		}
		&--right-tr {
			&::after {
				top: 50px;
				right: -20px;
				border-color: transparent transparent transparent #ffffff;
				@media (max-width: 770px) {
					top: -20px;
					left: 50px;
					border-color: transparent transparent #ffffff transparent ;
				}
			}
		}
		&--top-tr {
			&::after {
				top: -20px;
				left: 50px;
				border-color: transparent transparent #ffffff transparent ;
				@media (min-width:770px) and (max-width:1200px) {
					top: 50px;
					left: -20px;
					border-color: transparent #ffffff transparent transparent;
				}
			}
		}
		@media (max-width: 770px) {
			padding: 35px 35px 60px 35px;
		}
	}
	&__about {
		display: block;
		width: 100%;
		.ff-700();
		color: #16b5ef;
		font-size: 11px;
		line-height: 12px;
		letter-spacing: 1.74px;
		text-align: center;
	}
	&__subtitle {
		height: 72px;
		margin-top: 50px;
		.ff-700();
		text-align: center;
		line-height: 24px;
		letter-spacing: 2.55px;
		color: #212121;
		overflow: hidden;
		@media (max-width: 770px) {
			height: 45px;
			margin-top: 35px;
		}
		span {
			position: relative;
			&::after {
				content:'';
				position: absolute;
				width: 105%;
				height: 2px;
				top: 50%;
				left: -5px;
				transform: translateY(-50%);
				background-color: #000000;
			}
		}
	}
	&__text {
		max-height: 88px;
		margin-top: 27px;
		padding: 0 20px;
		text-transform: none;
		text-align: center;
		font-size: 14px;
		line-height: 22px;
		color: #777777;
		overflow: hidden;
		@media (max-width: 770px) {
			padding: 0;
			font-size: 16px;
		}
	}
	&__counter {
		position: absolute;
		bottom: 35px;
		left: 50%;
		transform: translateX(-50%);
		font-size: 14px;
		line-height: 17px;
		color: #212121;
		@media (max-width: 770px) {
			bottom: 21px;
		}
	}
}
.reverse {
	.journal-article__content {
		order: 0;
	}
	.journal-article__img {
		order: 1;
	}
}
.icon{
	&::after {
		content:'';
		position: absolute;
		width: 81px;
		height: 81px;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		background-size: contain;
	}
	&--heart {
		&::after {
			background: url(../img/heart.png) no-repeat center center;
		}
	}
	&--leaf {
		&::after {
			background: url(../img/leaf.png) no-repeat center center;
		}
	}
	&--drop {
		&::after {
			background: url(../img/drop.png) no-repeat center center;
		}
	}
}
///------steps----------------///
.our-work-s {
	padding-bottom: 50px;
}
.steps {
	position: relative;
	display: flex;
	margin-top: 75px;
	@media(max-width: 1024px) {
		margin-top: 40px;
		flex-wrap: wrap;
		justify-content: center;
	}
	@media(max-width: 600px) {
		margin-top: 15px;
	}
	&::after {
		content:'';
		position: absolute;
		width: 75%;
		height: 2px;
		left: 50%;
		top: 0;
		transform: translateX(-50%);
		background: url(../img/dashv3.svg) repeat-x center;
		background-size: contain;
		z-index: 1;
		@media(max-width: 1024px) {
			display: none;
		}
	}
}
.step {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex: 0 1 25%;
	min-width: 245px;
	padding: 0 25px;
	padding-top: 80px;
	text-align: center;
	@media(max-width: 1024px) {
		flex: 0 1 50%;
		padding: 20px 25px;
	}
	@media(max-width: 600px) {
		flex: 0 1 100%;
		padding: 15px 25px;
	}
	&__count {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 48px;
		height: 48px;
		top: 0;
		left: 50%;
		transform: translate(-50%,-50%);
		color: #3bb0eb;
		font-family: "Roboto", "Arial", sans-serif;
		font-size: 16px;
		font-weight: 900;
		text-transform: uppercase;
		border: 3px solid #36aeea;
		border-radius: 50%;
		background-color: #ffffff;
		overflow: hidden;
		z-index: 20;
		@media(max-width: 1024px) {
			display: none;
			visibility: hidden;
		}
	}
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-family: "Roboto", "Arial", sans-serif;
		z-index: 2;
		&::after {
			content:'';
			position: absolute;
			width: 2px;
			height: 75px;
			top: 0;
			left: 50%;
			transform: translate(-50%,-100%);
			background: url(../img/dashv2.svg) repeat center;
			background-size: contain;
			@media(max-width: 1024px) {
				display: none;
			}
		}
	}
	&__img {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 179px;
		height: 179px;
		border-radius: 50%;
		background:#f0f3f6 url(../img/step-poly.svg) no-repeat 50% 58%;
		background-size: 70%;
		@media(max-width: 600px) {
			width: 150px;
			height: 150px;
		}
		img {
			width: 100%;
			max-height: 40px;
			position: relative;
		}
	}
	&__title {
		margin-top: 25px;
		font-family: "Roboto", "Arial", sans-serif;
		font-size: 18px;
		font-weight: 900;
		color: #2c2e37;
		@media(max-width: 600px) {
			margin-top: 20px;
		}
	}
	&__discr {
		margin-top: 5px;
		font-family: "Roboto", "Arial", sans-serif;
		font-size: 18px;
		font-weight: 300;
		color: #949494;
		@media(max-width: 600px) {
			//font-size: 16px;
		}
	}
}
