////-------gallery-----///
.gal-s {
	margin-top: 70px;
	@media(max-width: 960px) {
		margin-top: 50px;
	}
}
.gallery {
	margin-top: 50px;
	font-size: 0;
	@media(max-width: 960px) {
		margin-top: 30px;
	}
	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: flex-start;
		margin: 0 -15px;
		@media(max-width: 1200px) {
			margin: 0;
		}
	}
	img {
		object-fit: cover;
	}
	&__column {
		display: flex;
		flex-direction: column;
		margin: 0 15px;
		@media(max-width: 1200px) {
			margin: 0 5px;
		}
	}
	&__column-1 {
		flex:0 1 22.4%;
		font-size: 0;
		@media(max-width: 960px) {
			flex:0 1 46%;
		}
		@media(max-width: 600px) {
			flex:0 1 100%;
		}
		.gallery__item {
			
			&:nth-of-type(1) {
				img {
					height: 343px;
				}
			}
			&:nth-of-type(2) {
				img {
					height: 281px;
				}
			}	
		}
	}
	&__column-2 {
		flex:0 1 30.85%;
		font-size: 0;
		@media(max-width: 960px) {
			flex:0 1 50%;
		}
		@media(max-width: 600px) {
			flex:0 1 100%;
		}
		.gallery__item {
			&:nth-of-type(1) {
				img {
					height: 242px;
				}
			}
			&:nth-of-type(2) {
				img {
					height: 382px;
				}
			}	
		}
	}
	&__column-3 {
		flex:0 1 38.89%;
		font-size: 0;
		@media(max-width: 960px) {
			flex:0 1 100%;
		}
		.gallery__item {
			&:nth-of-type(1) {
				img {
					height: 343px;
				}
			}
			&:nth-of-type(2) {
				img {
					height: 281px;
				}
			}	
		}
	}
	&__item {
		margin: 15px 0;
		&:hover img{
			transform: scale(1.01);
		}
		@media(max-width: 1200px) {
			margin: 5px 0;
		}
		img {
			transition: 0.2s;
		}
	}
}
//-------------brands-------------//
.brands-s {
	margin-top: 60px;
	background-color: #f8f9fa;
}
.brands {
	position: relative;
	min-width: 0;
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		min-height: 180px;
		padding: 10px;
		cursor: pointer;
		outline: none;
		&:hover img {
			filter: drop-shadow(10px 10px 5px black);
		}
		@media(max-width: 770px) {
			min-height: 120px;
		}
	}
	img {
		width: auto;
		height: auto;
		transition: 0.2s;
	}
}
////--------------clients-------------------///
.client-wrap {
	position: relative;
	min-height: 1335px;
	background: url('@{bg}client-bg.jpg') no-repeat bottom center;
}
.responses-s__title {
	margin-top: 65px;
}
