}
///------------form-----------///
.form {
	margin-top: 15px;
	@media	(max-width:450px) {
		margin-top: 0;
	}
	&__input {
		width: 100%;
		margin-top: 20px;
		padding: 23px 19px;
		color: #383e4a;
		outline: none;
		border-radius: 4px;
		border: 1px solid transparent;
		transition: 0.5s ease;
		@media	(max-width:450px) {
			padding: 17px 15px;
		}
		&:hover,
		&:focus {
			border-color: #16b5ef;
		}
		&::placeholder {
			.ff-r();
			font-size: 18px;
			color: #cccccc;
		}
		&:focus::placeholder {
			color: transparent;
		}
	}
	&__btn {
		width: 100%;
		margin-top: 20px;
		padding: 20px 30px;
		font-weight: 700;
		color: #ffffff;
		letter-spacing: 2.56px;
		text-align: center;
		text-transform: uppercase;
		cursor: pointer;
		border-radius: 8px;
		border: none;
		background-color: #36aeea;
	}
}
.contact-form {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	max-width: 515px;
	margin: 0 auto;
	margin-top: 120px;
	margin-bottom: 85px;
	padding: 50px 30px;
	font-family: @ff;
	border-radius: 10px;
	background-color: rgba(204,204,204,.5);
	box-shadow: 0 5px 51px rgba(0, 0, 0, 0.75);
	@media	(max-width:450px) {
		margin-top: 60px;
		padding: 30px 20px;
	}
	&__title {
		font-size: 36px;
		font-weight: 700;
		color:#36aeea;
		text-transform: uppercase;
		letter-spacing: 3.6px;
		@media	(max-width:450px) {
			font-size: 28px;
		}
	}
	&__discr {
		color: #383e4a;
	}
}
///---------slider-----------///
.slick-list {
		width: 100%;
		overflow: hidden;
	}
	.slick-track{
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
	}